
.header{
	background-color: #CAE9EC;
	padding: 10px 20px;
}
.float_l{
	float:left;
}
.float_r{
	float: right;
}
.gray-words{
	color:gray;
}
.clearfix:after{
	content: "";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}
.clearfix{
	zoom:1;
}
.logo{
	display:block;
	margin-right:20px;
}
.title{
	margin-top:5px;
}
.searchinput{
	width: 100px;
	height: 20px;
	border: none;
	margin-top:13px;
	border-radius: 12px;
	padding:1px 10px;
}
.nav-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: #0C65CC;
}
.v-nav {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    text-decoration: none;
    height: 35px;
    line-height: 35px;
    font-size: 20px;
    color: #F9FAFC;
}




